<template>
    <div class="other">
        <h1>{{msg}}</h1>
        <div class="charts">
          <div id="charts">
              <div ref="main" id="main"  :style="{width:'600px',height:'400px'}"></div>
          </div>
        </div>
        <button type="button" name="button" @click="jump"></button>
    </div>
</template>

<script>
   import router from '../../router'
   //
  // import echarts from 'echarts'
   //  ECharts  
  var echarts = require('echarts/lib/echarts');
  // 
  require('echarts/lib/chart/bar');
  // 
  require('echarts/lib/component/tooltip');
  require('echarts/lib/component/title');

    export default {
        name: "Other",
        data() {
            return {
                msg: "hello Other"
            }
        },
        methods:{
          jump:function(){
            router.go(-1);
          }
        },
        mounted:function(){
          // domecharts
          var myChart = echarts.init(this.$refs.main);
          // 
          var option = {
              title: {
                  text: 'ECharts '
              },
              tooltip: {},
              legend: {
                  data:['']
              },
              xAxis: {
                  data: ["","","","","",""]
              },
              yAxis: {},
              series: [{
                  name: '',
                  type: 'bar',
                  data: [5, 20, 36, 10, 10, 20]
              }]
          };
          // 
          myChart.setOption(option);
        }
    }
</script>
<style lang='scss' scoped>
  .other{
    .charts{
      overflow: hidden;
      height: 380px;
      >div{
        position: relative;
        overflow-x:auto;
        height: 400px;
        >div{
          position: absolute;
          left:0;
          top:0;
        }
      }
    }
  }
</style>



